	module.exports = {"data":{"markdownRemark":{"html":"<p>Web Designers everywhere are taking a break. </p>\n<p>Sometime about 5 years ago people began to realize the frustrating limits of web development because the existing standards were so poorly followed by existing browsers. It was something like what Frost said about \"poetry without rhyme is like playing tennis without a net.\" </p>\n<p>Which is to say, no fun at all. Online communication has progressed steadily since then, and now we have wonderful fruits like \"AJAXy goodness\" and other Web 2.0 technologies to reap. So take a break from griping about Netscape vs. Internet Explorer. </p>\n<p>But Google Maps (etc.) being accomplished, we're looking for ever greater technologies. The next frontier of web development is the mobile browser — the web in a cell phone. </p>\n<p>The concept of the mobile web has been huge this year, and it is now taken as a fact that most internet users in the next 5 years will be getting online for the first time through their cell phones. </p>\n<p>And so, with that in mind, here's a great series of articles from designer Cameron Moll that looks at the background of the mobile web and explores the specifics of developing +designing reliable, readable sites for really, really small screens. </p>\n<blockquote class=\"large\">How do we designers and code slingers cope with the current state? What slings and what doesn'? This article attempts to present technical advice on a superficial level. </blockquote>\n<p><a href=\"http://www.cameronmoll.com/archives/000577.html\">Mobile Web Design: Tips &#x26; Techniques ~ Authentic Boredom</a></p>","frontmatter":{"created_at":"December 20, 2005","path":"/2005/12/20/mobile-web-design-tips-techniques-authentic-boredom","title":"Mobile Web Design: Tips & Techniques (Technical)","tags":["design","mobile","webdev"]}}},"pathContext":{}}

